import React, { Component } from 'react';
import { Card, CardHeader, Col, Row, Button, Input, FormGroup, Label} from 'reactstrap';

//import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

//import Loader from '../../../Loader/Loader';
import PaymentData from './PaymentData';
import './Payment.css'

class Payment extends Component {
  constructor(props){
    super(props);
    this.state = {
      formField: {}
    }
    
    
  }
  // Fetch the Employee List
  componentDidMount() { 
   
  }

    
  

  render() {

       
    
    return (
      <div className="animated fadeIn">
        <Row>
          <Col lg={12}>
            <Card className="oep-card">
              <CardHeader className="mainHeading">
                <strong>Payment List</strong> 
                  </CardHeader>
                <Row>
                  <Col md={12} className="search-filter">
                    <Row>
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_Paitent_id">Paitent</Label>            
                          <Input type="text" placeholder="Paitent" id="custom_search" name="custom_search" value={this.state.formField.custom_search}  />
                        </FormGroup>  
                      </Col>
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_accept_trial">Search By Clinical Trial</Label>            
                          <Input type="text" placeholder="Search By Clinical Trial" id="filter_accept_trial" name="filter_accept_trial" value={this.state.formField.filter_accept_trial}  />
                        </FormGroup>  
                      </Col>
                      
                      <Col md={"3"}>
                        <FormGroup className="filter-button-section"> 
                          <Label htmlFor="filter_organization_id">&nbsp;</Label> 
                          <Button color="success" className="search-btn" type="button">Search</Button> 
                          <Button color="warning" className="search-btn" id="resetButton" type="button" >Reset</Button> 
                        </FormGroup>             
                      </Col>
                    </Row>  
                  </Col>
                  <Col md={12}>
                    <PaymentData data={[]} title = "Due Payment"  />
                  </Col>
                </Row> 
                <br />
                
                
                <Row>
                  <Col md={12} className="search-filter">
                    <Row>
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_Paitent_id">Paitent</Label>            
                          <Input type="text" placeholder="Paitent" id="custom_search" name="custom_search" value={this.state.formField.custom_search}  />
                        </FormGroup>  
                      </Col>
                      <Col md={"3"}>
                        <FormGroup> 
                          <Label htmlFor="filter_accept_trial">Search By Clinical Trial</Label>            
                          <Input type="text" placeholder="Search By Clinical Trial" id="filter_accept_trial" name="filter_accept_trial" value={this.state.formField.filter_accept_trial}  />
                        </FormGroup>  
                      </Col>
                      
                      <Col md={"3"}>
                        <FormGroup className="filter-button-section"> 
                          <Label htmlFor="filter_organization_id">&nbsp;</Label> 
                          <Button color="success" className="search-btn" type="button">Search</Button> 
                          <Button color="warning" className="search-btn" id="resetButton" type="button" >Reset</Button> 
                        </FormGroup>             
                      </Col>
                    </Row>  
                  </Col>
                  <Col md={12}>
                    <PaymentData data={[]} title = "Payable Payment"  />
                  </Col>
                </Row> 
            </Card>
          </Col>
        </Row>
        
      </div>

    )
  }
}



export default Payment;
